.App{
  text-align: center;
}

.box{
  max-width: 400px;
  
  margin: 50px auto;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  padding: 50px;
  background-color:gold;
}

p{
  margin-top: 20px;
  font-size: 50px;
  text-align: center;
}

button{
  
  padding: 50px;
  margin: 20px 10px;
  font-size: 16px;
  color: #FFFFFF;
  border: 0px;
  border-radius: 5px;
  cursor: pointer;
  
}

button.add{
  

  width: 100px;       /* Adjust the width to control the size of the circle */
  height: 100px;      /* The height should be the same as the width to make it a perfect circle */
  border-radius: 50%; /* This makes the button circular */
  background-color:red; /* Button color (you can change this) */
  color: white;      /* Text color */
  border: none;      /* No border */
  display: inline-flex; /* Align the text/icon in the center */
  align-items: center;
  justify-content: center;
  font-size: 50px;   /* Font size for text or icon */
  cursor: pointer;   /* Change cursor to pointer on hover */
  transition: background-color 0.3s ease;
  margin-right: 50px;
}

button.sub{
  background-color:red;
  max-width: 150px;
}

button.Reset{
  
  
  width: 1px;       /* Adjust the width to control the size of the circle */
  height: 1px;      /* The height should be the same as the width to make it a perfect circle */
  border-radius: 50%; /* This makes the button circular */
  background-color:black; /* Button color (you can change this) */
  color: white;      /* Text color */
  border: none;      /* No border */
  display: inline-flex; /* Align the text/icon in the center */
  align-items: center;
  justify-content: center;
  font-size: 16px;   /* Font size for text or icon */
  cursor: pointer;   /* Change cursor to pointer on hover */
  transition: background-color 0.3s ease;
  
  
}







.left-image {
  width: 100px;                   /* Adjust the width as needed */
  height: auto;                   /* Maintain aspect ratio */
}

.center-image {
  width: 100px;                   /* Adjust the width as needed */
  height: auto;                   /* Maintain aspect ratio */
  margin-left: 100px;              /* Push the second image to the center */
  margin-right: 800px;             /* Ensure it's centered within the remaining space */
}


